'use client';

import { useRouter } from 'next/navigation';
import { useState } from 'react';

export default function SearchForm({ heroData }) {
  const router = useRouter();
  const [formData, setFormData] = useState({
    location: '',
    category: '',
  });

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const query = new URLSearchParams();
    if (formData.location) query.set('location', formData.location);
    const url = formData.category 
      ? `/properties/${formData.category}?${query.toString()}`
      : `/properties?${query.toString()}`;
    router.push(url);
  };

  return (
    <form onSubmit={handleSubmit} className="wow fadeInUp" data-wow-delay=".3s">
      <div className="row g-4 align-items-center justify-content-between">
        <div className="col-xl-3 col-lg-4 col-md-6 col-sm-6">
          <div className="form-item">
            <h6>Location</h6>
            <input
              type="text"
              name="location"
              placeholder="Looking For?"
              value={formData.location}
              onChange={handleChange}
            />
          </div>
        </div>
        <div className="col-xl-3 col-lg-4 col-md-6 col-sm-6">
          <div className="form-item">
            <h6>Category</h6>
            <div className="form">
              <select
                name="category"
                className="single-select w-100"
                value={formData.category}
                onChange={handleChange}
              >
                <option value="">Select Category</option>
                {heroData.searchCategories.map((cat, idx) => (
                  <option key={idx} value={cat.value} className="text-black">
                    {cat.name}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>
        <div className="col-xl-3 col-lg-4 col-md-6 col-sm-6">
          <div className="form-item">
            <h6>Location</h6>
            <div className="form">
              <select
                name="location"
                className="single-select w-100"
                value={formData.location}
                onChange={handleChange}
              >
                <option value="">Select Location</option>
                {heroData.searchLocations.map((loc, idx) => (
                  <option key={idx} value={loc.value} className="text-black">
                    {loc.name}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>
        <div className="col-xl-3 col-lg-4 col-md-6 col-sm-6">
          <div className="form-item text-align-right">
            <button type="submit" className="theme-btn">
              Search <i className="fa-solid fa-magnifying-glass"></i>
            </button>
          </div>
        </div>
      </div>
    </form>
  );
}